<template>
  <div class="recommendRank">
    <Title :title="$attrs.title" :loading="false" :typeBooks="bookRanking">
      <slot></slot>
    </Title>
    <van-grid direction="horizontal" :column-num="2">
      <van-grid-item
        v-for="(item, index) in bookRanking.slice(0, 8)"
        :key="item._id"
        @click="toBookdetail(item._id)"
      >
        <!-- rank排行的小图标：开始 -->
        <template v-if="index === 0">
          <span class="rankPoint" style="background: rgb(241, 201, 109)">{{
            index + 1
          }}</span>
        </template>
        <template v-else-if="index === 1">
          <span class="rankPoint" style="background: rgb(181, 183, 190)">
            {{ index + 1 }}
          </span>
        </template>
        <template v-else-if="index === 2">
          <span class="rankPoint" style="background: rgb(230, 158, 107)">{{
            index + 1
          }}</span>
        </template>
        <template v-else>
          <span class="rankPoint">{{ index + 1 }}</span>
        </template>
        <!-- rank排行的小图标：结束 -->

        <van-image :src="`http://statics.zhuishushenqi.com${item.cover}`" />
        <div class="content">
          <section>
            <p class="content-title">{{ item.title }}</p>
            <p class="content-author">
              <img src="../../public/img/author.svg" alt="" srcset="" />
              {{ item.author }}
            </p>
            <p class="content-type" v-if="item.minorCate">
              {{ item.minorCate }}
            </p>
          </section>
        </div>
      </van-grid-item>
    </van-grid>
  </div>
</template>

<script>
import Title from "@/components/Title.vue";
export default {
  components: {
    Title,
  },
  props: ["bookRanking"],
  methods: {
    toBookdetail(id) {
      // console.log(id);
      this.$router.push({
        path: "/bookDetail/" + id,
        // path: "/bookDetail",
        // query: {
        //   id: id,
        // },
      });
    },
  },
};
</script>

<style lang="less">
.recommendRank {
  min-height: 440px;
  margin-bottom: 30px;
  position: relative;
  &::after {
    content: "";
    width: 200%;
    height: 1px;
    background: #6666663b;
    display: block;
    position: absolute;
    bottom: -17px;
    left: -50%;
    transform: scale(0.5);
  }
  .van-hairline--top::after {
    border: none;
  }
  .van-grid {
    .van-grid-item {
      margin-top: 20px;
      position: relative;
      &::after {
        border: none;
      }
      &:nth-of-type(1),
      &:nth-of-type(2) {
        margin-top: 0;
      }
      .rankPoint {
        z-index: 2;
        display: block;
        position: absolute;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        text-align: center;
        line-height: 20px;
        color: #fff;
        font-size: 12px;
        background: #5697f2;
        top: 76px;
        left: 58px;
      }
      .van-grid-item__content {
        display: flex;
        padding: 0;
        padding-left: 3px;
        &::after {
          border: none;
        }
        .van-image {
          width: 68px;
          height: 88px;
          margin: 0;
          .van-image__img {
            border-radius: 4px;
            box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
          }
        }
        .content {
          flex: 1;
          font-size: 12px;
          height: 88px;
          section {
            height: 100%;
            margin-left: 10px;
            padding: 5px 0;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .content-title {
              color: #000;
            }
            .content-author {
              color: #999;
              img {
                width: 14px;
                height: 14px;
                vertical-align: middle;
              }
            }
            .content-type {
              max-width: 60px;
              height: 20px;
              text-align: center;
              line-height: 20px;
              font-size: 8px;
              border-radius: 7px;
              color: #777;
              background: #efeff4;
            }
          }
        }
      }
    }
  }
}
</style>