<template>
  <div class="home">
    <!-- <HomeNav /> -->
    <!-- recommend的内容 -->
    <!-- <template v-if="$route.meta.isShowRecommend"> -->
    <div class="recommend">
      <RecommendedSwiper :swiperBook="maleFinish.slice(0, 8)" />
      <section>
        <h2><van-icon name="fire-o" color="#ee0a24" />人气榜单</h2>
        <RecommendRank title="男生最热榜单" :bookRanking="maleMostHot">
          男生最热榜单
        </RecommendRank>
        <RecommendRank title="女生最热榜单" :bookRanking="femaleMostHot">
          女生最热榜单
        </RecommendRank>
      </section>
      <section>
        <h2>
          <img src="../../public/img/finish.svg" class="icon-finish" />完结榜单
        </h2>
        <RecommendRank title="男生完结榜单" :bookRanking="maleFinish">
          男生完结榜单
        </RecommendRank>
        <RecommendRank title="女生完结榜单" :bookRanking="femaleFinish">
          女生完结榜单
        </RecommendRank>
      </section>
      <BackToTop />
    </div>
    <!-- </template> -->

    <!-- 其他路由的渲染出口 -->
    <!-- <template v-else>
      <router-view></router-view>
    </template> -->
  </div>
</template>

<script>
// import HomeNav from "@/components/HomeNav.vue";
import RecommendedSwiper from "@/components/RecommendedSwiper.vue";
import RecommendRank from "@/components/RecommendRank.vue";
import BackToTop from "@/components/BackToTop.vue";
export default {
  components: {
    RecommendedSwiper,
    // HomeNav,
    RecommendRank,
    BackToTop,
  },
  data() {
    return {
      show: true,
      maleMostHot: [],
      femaleMostHot: [],
      maleFinish: [],
      femaleFinish: [],
      // swiperbook: [],
      count: 0, //优化的计算值
    };
  },
  watch: {
    count(n) {
      if (n >= 4) {
        this.$root.isShowLoading = false;
      }
    },
  },
  created() {
    document.documentElement.scrollTop = 0;
    this.search();
  },
  // activated() {
  //   document.documentElement.scrollTop = 0;
  // },
  methods: {
    search() {
      // this.axios.get("/rank/5a6844f8fc84c2b8efaa8bc5").then((res) => {
      //   this.swiperbook = [...res.data.ranking.books.slice(0, 5)];
      // });
      this.$root.isShowLoading = true;
      if (
        localStorage.getItem("maleMostHot") &&
        JSON.parse(localStorage.getItem("maleMostHot")).expire > Date.now()
      ) {
        this.maleMostHot = JSON.parse(
          localStorage.getItem("maleMostHot")
        ).books;
        // console.log("男生最热榜已存入本地存储");
        this.count++;
      } else {
        this.axios
          .get("/rank/5a6844f8fc84c2b8efaa8bc5")
          .then((res) => {
            // console.log("男生最热榜查询", res);
            let obj = {
              books: res.data.ranking.books.slice(0, 30),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            localStorage.setItem("maleMostHot", JSON.stringify(obj));
            this.maleMostHot = res.data.ranking.books.slice(0, 30);
          })
          .finally(() => {
            this.count++;
          });
      }

      if (
        localStorage.getItem("femaleMostHot") &&
        JSON.parse(localStorage.getItem("femaleMostHot")).expire > Date.now()
      ) {
        this.femaleMostHot = JSON.parse(
          localStorage.getItem("femaleMostHot")
        ).books;
        // console.log("女生最热榜已存入本地存储");
        this.count++;
      } else {
        this.axios
          .get("/rank/54d43437d47d13ff21cad58b")
          .then((res) => {
            // console.log("女生最热榜查询", res);
            let obj = {
              books: res.data.ranking.books.slice(0, 30),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            localStorage.setItem("femaleMostHot", JSON.stringify(obj));
            this.femaleMostHot = res.data.ranking.books.slice(0, 30);
          })
          .finally(() => {
            this.count++;
          });
      }

      if (
        localStorage.getItem("maleFinish") &&
        JSON.parse(localStorage.getItem("maleFinish")).expire > Date.now()
      ) {
        this.maleFinish = JSON.parse(localStorage.getItem("maleFinish")).books;
        // console.log("男生完结榜已存入本地存储");
        this.count++;
      } else {
        this.axios
          .get("/rank/564eb878efe5b8e745508fde")
          .then((res) => {
            // console.log("男生完结榜查询", res);
            let obj = {
              books: res.data.ranking.books.slice(0, 30),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            localStorage.setItem("maleFinish", JSON.stringify(obj));
            this.maleFinish = res.data.ranking.books.slice(0, 30);
          })
          .finally(() => {
            this.count++;
          });
      }

      if (
        localStorage.getItem("femaleFinish") &&
        JSON.parse(localStorage.getItem("femaleFinish")).expire > Date.now()
      ) {
        this.femaleFinish = JSON.parse(
          localStorage.getItem("femaleFinish")
        ).books;
        // console.log("女生完结榜已存入本地存储");
        this.count++;
      } else {
        this.axios
          .get("/rank/564eb8a9cf77e9b25056162d")
          .then((res) => {
            // console.log("女生完结榜查询", res);
            let obj = {
              books: res.data.ranking.books.slice(0, 30),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            localStorage.setItem("femaleFinish", JSON.stringify(obj));
            this.femaleFinish = res.data.ranking.books.slice(0, 30);
          })
          .finally(() => {
            this.count++;
          });
      }
    },
  },
};
</script>

<style lang="less">
.recommend {
  width: 100%;
  box-sizing: border-box;
  margin: 90px 0;
  padding: 0 10px;
  box-sizing: border-box;
  h2 {
    text-align: center;
    font-size: 18px;
    font-family: "Arial";
    font-weight: 400;
    .van-icon-fire-o {
      vertical-align: middle;
      margin-right: 5px;
    }
    .icon-finish {
      width: 30px;
      height: 30px;
      vertical-align: middle;
      margin-right: 5px;
    }
  }
}
</style>