<template>
  <!-- 去往相应的详情 -->
  <div
    class="recommendedswiper"
    @touchstart.stop
    @touchmove.stop
    @touchend.stop
    v-if="swiperBook.length > 0"
  >
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div
          class="swiper-slide"
          v-for="(item, index) in swiperBook"
          :key="index"
        >
          <img
            class="swiper-img"
            :src="'http://statics.zhuishushenqi.com' + item.cover"
            alt
            @click="toBookdetail(item._id)"
          />
        </div>
      </div>
    </div>
    <!-- <div class="aaa"> -->
    <template v-if="swiperBook.length > 0">
      <div class="swiper-title">
        {{ swiperBook[index].title }}
      </div>
      <div class="swiper-shortIntro">
        {{ swiperBook[index].shortIntro }}
      </div>
    </template>
    <!-- </div> -->
  </div>
</template>

<script>
import Swiper from "swiper";
import "../../node_modules/_swiper@6.4.5@swiper/swiper-bundle.min.css";
export default {
  props: ["swiperBook"],
  data() {
    return {
      swiper: null,
      index: 0,
    };
  },
  mounted() {
    var that = this;
    that.$nextTick(function () {
      this.swiper = new Swiper(".swiper-container", {
        // eslint-disable-line no-unused-vars
        slidesPerView: 4,
        loop: true,
        spaceBetween: 5,
        // freeMode: true,
        centeredSlides: true,
        // watchSlidesProgress: true,
        on: {
          slideChange: function () {
            that.index = this.realIndex;
          },
        },
      });
    });
  },
  methods: {
    toBookdetail(id) {
      this.$router.push({
        path: "/bookDetail/" + id,
      });
    },
  },
};
</script>

<style scoped >
.swiper-container {
  width: 100%;
  height: 120px;
  margin-top: 10px;
  overflow: hidden;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  transition: 300ms;
  transform: scale(0.8);
  background-repeat: no-repeat;
  background-size: contain;
}

.swiper-img {
  border-radius: 4px;
}

.swiper-title {
  /* width: 100vw; */
  text-align: center;
  font-weight: bold;
  margin-top: 10px;
}
.swiper-shortIntro {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 40px;
  margin-bottom: 10px;
  font-size: 14px;
}
.swiper-slide-active,
.swiper-slide-duplicate-active {
  transform: scale(1);
}
</style>